    import {  
        mapState,  
        mapMutations  
    } from 'vuex';  
    export default { 
		data() {
			return {
				act_info:[],
				memberInfo:[],
				money:0
			}				
		},
        computed: mapState(['hasLogin','userInfo','Location']),  
		onLoad(e) {
			let _this = this;
			_this.city = _this.Location.city_name;
			console.log('页面加载');
			if (_this.hasLogin) { 
				_this.$Request.post('plat_act/act_subjoin',{id:e.act_id}).then(res => {
					if (res.code == 200) {
						console.log(res);
						_this.act_info = res.datas.act_detail;
						_this.memberInfo = res.datas.member_info;
						_this.money = _this.act_info.act_price;
					}
					else{
						uni.showToast({
							icon: 'none',
							title:res.datas.error,
							duration:2000
						});
						
					}
				 });
			}
		},
         onReachBottom() {
             console.log('正在加载中...');
         },
         onPageScroll(){
             console.log('页面滚动...');
         },
         onPullDownRefresh() {
             console.log('上拉刷新...');
         },
        methods: {
			
			numberChange(data){
				console.log(data);
				let num = data.detail.value;
				this.money = num*this.act_info.act_price;
				//this.cart_List[data.index].number = data.number;
				//this.calcTotal();
			},
            toPage: function(url) {
				if (!this.hasLogin) {
					url="/pages/member/login";
				} 
				uni.navigateTo({
					url:url,
				})
				
			},
        }  
    }  
